/* General styles */

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 14px;
  text-size-adjust: 100%;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  line-height: 1.4285em;
  color: rgba(0, 0, 0, 0.87);
}

body {
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen-Sans,
    Ubuntu,
    Cantarell,
    "Helvetica Neue",
    sans-serif;
}

/* Page */
.page {
  position: relative;
  padding: 3em;
  background: white;
  box-shadow: 0 10px 100px 0 rgba(34, 36, 38, 0.1);
  transition: box-shadow 0.2s linear;

  &[style^="border-color"] {
    padding: 4.5em;
  }

  &[style^="border-color"]:before {
    content: " ";
    display: block;
    position: absolute;
    left: 2em;
    top: 2em;
    right: 2em;
    bottom: 2em;
    border-color: inherit;
    border-width: 1em;
    border-style: solid;
    pointer-events: none;
    transition: border-color 0.2s ease-in-out;
  }
}

@media only screen {
  .page:hover {
    box-shadow: 0 10px 100px 0 rgba(34, 36, 38, 0.3);
  }
}

@media only screen and (max-width: 767px) {
  .page,
  .page[style^="border-color"] {
    padding: 0;
  }

  .page[style^="border-color"]:before {
    display: none;
  }
}

@media print {
  .page,
  .page[style] {
    padding: 0;
    box-shadow: none;
    border-color: transparent !important;
  }
}

section {
  padding: 1em 0;
}

p {
  margin: 0 0 1em;
  line-height: 1.4285em;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

strong {
  font-weight: 600;
}

small {
  font-size: 80%;
}

/* Article */
article {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  article {
    width: 723px;
    margin-top: calc((100% - 723px) / 2);
    margin-bottom: calc((100% - 723px) / 2);
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  article {
    width: 933px;
    margin-top: calc((100% - 933px) / 2);
    margin-bottom: calc((100% - 933px) / 2);
  }
}

@media only screen and (min-width: 1200px) {
  article {
    width: 1127px;
    margin-top: calc((100% - 1127px) / 2);
    margin-bottom: calc((100% - 1127px) / 2);
  }
}

/* Highlighted list item */
.highlighted {
  padding: 1em !important;
  background: rgba(0, 0, 0, 0.04);
  border-radius: 4px;
}


main {
  background: white;
  position: relative;
}

footer {
  width: 100%;
  text-align: center;
  padding: 2rem 0;
}

@media print {
  footer {
    display: none;
  }
}

/* Remove the border on images inside links in IE 10 */
img {
  border-style: none;
}

/* Change the font styles in all browsers, remove the margin in Firefox and Safari */
button,
input {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  overflow: visible;
}

/* Remove the inheritance of text transform in Edge, Firefox, and IE */
button {
  text-transform: none;
}

/* Correct the inability to style clickable types in iOS and Safari	*/
button,
[type="button"] {
  appearance: button;
}

/* Add the correct box sizing in IE 10, remove the padding in IE 10 */
[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

/* Prevent `sub` and `sup` elements from affecting the line height in all browsers */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Utiltities */
.nowrap {
  white-space: nowrap;
}

code {
  border: 1px solid #bdbdbd;
  padding: 0em 0.25em;
  border-radius: 3px;
  background: #eee;
  font-size: 95%;
}